.home-container {
    min-height: 100vh;
    background-color: #1a202c;
    color: #e2e8f0;
    padding: 2rem;
  }
  
  .content {
    max-width: 48rem;
    margin: 0 auto;
  }
  
  .title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 2rem;
  }
  
  .button-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    margin-bottom: 2rem;
  }
  
  .red-button,
  .blue-button,
  .yellow-button,
  .green-button {
    display: inline-block;
    padding: 0.5rem 1rem;
    border-radius: 0.25rem;
    text-decoration: none;
    font-weight: bold;
    text-align: center;
    transition: background-color 0.2s;
  }
  
  .red-button {
    background-color: #e53e3e;
    color: white;
  }
  
  .blue-button {
    background-color: #3182ce;
    color: white;
  }
  
  .yellow-button {
    background-color: #d69e2e;
    color: black;
  }
  
  .green-button {
    background-color: #38a169;
    color: white;
  }
  
  .red-button:hover,
  .blue-button:hover,
  .yellow-button:hover,
  .green-button:hover {
    opacity: 0.9;
  }
  
  .card {
    border: 1px solid #4a5568;
    border-radius: 0.5rem;
    padding: 1.5rem;
    margin-bottom: 2rem;
    text-align: center;
  }
  
  .icon-container {
    display: flex;
    justify-content: center;
    gap: 1rem;
  }
  
  .icon-button {
    background: none;
    border: none;
    color: #e2e8f0;
    font-size: 1.5rem;
    cursor: pointer;
    padding: 0.5rem;
    border-radius: 0.25rem;
    transition: background-color 0.2s;
  }
  
  .icon-button:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
  